import React, { useEffect, useState } from "react";
import "./assets/css/animate.css";
import "./assets/css/style.css";

import { Row, Col } from "reactstrap";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faEye,
  faTrashAlt,
  faTimesCircle,
} from "@fortawesome/free-regular-svg-icons";
import { faUserEdit } from "@fortawesome/free-solid-svg-icons";

import { withRouter, Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { deleteStory as deleteStoryAction } from "../redux/actions/topStoryAction";
import CircularProgress from "@material-ui/core/CircularProgress";
import API from "../redux/url";

const Category = () => {
  const [categories, setCategories] = useState(false);
  const [categories2, setCategories2] = useState(false);
  const [searchTxt, setSearchText] = useState(false);
  const dispatch = useDispatch();
  const isCategoryDeleted = useSelector(
    (state) => state.categoryReducer.isCategoryDeleted
  );

  const searchTextField = (e) => {
    e.preventDefault();
    let result = categories2.filter(
      (item) => item.category_name && item.category_name.toLowerCase().includes(e.target.value.toLowerCase())
    );
    setCategories(result);
  };
  useEffect(() => {
    axios.get(API + "/api/categories/get_all").then((result) => {
      setCategories(result.data);
      setCategories2(result.data);
    });
  }, [isCategoryDeleted]);

  const deleteStory = (id) => {
    dispatch(deleteStoryAction(id));
  };

  return (
    <>
      <div class="content-wrapper">
        <div
          id="order_preview"
          class="wow fadeInUp content_box"
          style={{ visibility: "visible", animationName: "fadeInUp" }}
        >
          {/* Companies Header and Search Bar Start */}

          <Row className="table-header">
            <Col xs="12" md="4">
              <h2 class="section-title">All Categories</h2>
            </Col>
            <Col xs="12" md="8">
              <form class="form-inline form-searchbar" action="#">
                <div class="form-group">
                  <input
                    type="text"
                    class="form-control"
                    placeholder="Start Typing to Search Story.."
                    onChange={searchTextField}
                  />
                </div>
                <Link to="/admin/CategoriesAddNew">
                  <button type="submit" class="btn btn-default">
                    Add New
                  </button>
                </Link>
              </form>
            </Col>
          </Row>

          {/* Companies Table Sart */}

          <div class="row">
            <div class="col-xs-12 col-md-12">
              <div class="table-responsive">
                {categories ? (
                  <table class="table table-bordered">
                    <tbody>
                      <tr>
                        <th class="active">S#</th>
                        <th class="active">Story Title</th>
                        <th class="active">Story desc</th>
                        <th class="active">Image</th>

                        <th class="active" style={{ width: "300px" }}>
                          Action
                        </th>
                      </tr>
                      {categories.map((item, ind) => {
                        return (
                          <tr>
                            <td>{ind + 1}</td>
                            <td>{item.category_name && item.category_name}</td>
                            <td
                              dangerouslySetInnerHTML={{
                                __html: item.description && item.description,
                              }}
                            ></td>
                            <td>
                              <img
                                src={API + "/api/uploads/" + item.image}
                                alt={item.image}
                                height="50"
                              />
                            </td>
                            <td>
                              {/* <FontAwesomeIcon
                                icon={faEye}
                                className="iconCompany"
                              /> */}

                              <FontAwesomeIcon
                                icon={faUserEdit}
                                className="iconCompany"
                              />

                              <FontAwesomeIcon
                                icon={faTrashAlt}
                                className="iconCompany"
                                onClick={() => deleteStory(item._id)}
                              />
                            </td>
                          </tr>
                        );
                      })}
                    </tbody>
                  </table>
                ) : (
                  <CircularProgress
                    color="secondary"
                    style={{
                      marginLeft: "45%",
                      marginTop: 30,
                      marginBottom: 30,
                    }}
                  />
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default withRouter(Category);
export let getCompanieslist;
